*{
    padding: 0;
    margin:0;
}
.budget-info{
    margin-top: 100px;
    width: 39%;
    padding-top: 8px;
    padding-bottom: 8px;
    text-align: center;
    color: white;
    border-radius: 1rem;
    background-color: rgba(33, 214, 124, 0.513);
    position: absolute;
    transform: translate(-50%, -50%);
    left:50%;
}
.header-index{
    position: absolute;
    top:250px;
    width: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
}
.header-income{
    width: 100%;
    height: 30px;
    padding-top: 8px;
    padding-bottom: 8px;
    border-radius: 1rem;
    background-color: rgba(29, 223, 223, 0.356);
}
.header-expense{
    margin-top: 10px;
    width: 100%;
    height: 30px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 1rem;
    background-color: rgba(231, 20, 20, 0.534);
}

.label-left{
    float: right;
    
}
label{
    font-size: 22px;
    padding-left: 5px;
    padding-right: 5px;
}

@media screen and (max-width: 600px) {
    .header-img{
        height: 350px;
    }
    .header-income{
        position: absolute;
        transform: translate(-50%, -50%);
        left:50%;
        width: 350px;
    }
    .header-expense{
        position: absolute;
        margin-top: 40px;
        transform: translate(-50%, -50%);
        left:50%;
        width: 350px;
    }
    .budget-info{
        position: absolute;
        padding-top: 5px;
        padding-bottom: 5px;
        margin-top: 40px;
        transform: translate(-50%, -50%);
        left:50%;
        background-color: rgba(33, 214, 124, 0.513);
        width: 350px;
    }
}
@media screen and (max-width: 768px) {
    .header-img{
        height: 340px;
    }
    .header-income{
        /* margin-top: 10px; */
        position: absolute;
        transform: translate(-50%, -50%);
        left:50%;
        margin-top: -70px;
        width: 300px;
    }
    .header-expense{
        position: absolute;
        transform: translate(-50%, -50%);
        left:50%;
        margin-top: -10px;
        width: 300px;
    }
    .budget-info{
        margin-top: 100px;
        width: 300px;
    }
}
@media screen and (max-width: 420px) {
    .header-img{
        height: 230px;
    }
    .header-income{
        /* margin-top: 10px; */
        position: absolute;
        margin-top: -110px;
        padding-top: 4px;
        padding-bottom: 2px;
        width: 300px;
    }
    .header-expense{
        position: absolute;
        margin-top: -60px;
        padding-top: 4px;
        padding-bottom: 2px;
        width: 300px;
    }
    .budget-info{
        position: absolute;
        margin-top: 70px;
        width: 300px;
    }
}
@media screen and (max-width: 320px) {
    .header-income{
        width: 280px;
    }
    .header-expense{
        width: 280px;
    }
    .budget-info{
        width: 280px;
    }
}
@media screen and (max-width: 284px) {
    .header-income{
        width: 250px;
    }
    .header-expense{
        width: 250px;
    }
    .budget-info{
        width: 250px;
    }
}