*{
    padding:0;
    margin: 0;
}
.incEditBtn{
    height: 25px;
    opacity: 0;
}

.incEditBtn:hover{
    opacity: 1;
    transition: 0.5s;
}
.des-opacity{
    display: inline-block;
    
}
.exp-div{
    width:500px;
}
.inc-div{
    width:500px;
}
.output-top{
   display: flex;
   justify-content: center;
   margin-top: 40px; 
}

@media screen and (max-width: 1024px) {
    .output-top{
        display: block;
        margin-top: 40px;
        position: absolute;       
        left: 50%;
        transform: translate(-50%);
     }
     .exp-div{
        width:700px;
        
    }
    .inc-div{
        width:700px;
    }
  }

  @media screen and (max-width: 720px) {
     .exp-div{
        width:500px;
        
    }
    .inc-div{
        width:500px;
    }
  }
  @media screen and (max-width: 510px) {
    .exp-div{
       width:400px;
       
   }
   .inc-div{
       width:400px;
   }
 }
 @media screen and (max-width: 410px) {
    .exp-div{
       width:300px;
       
   }
   .inc-div{
       width:300px;
   }
 }
 @media screen and (max-width: 310px) {
    .exp-div{
       width:260px;
       
   }
   .inc-div{
       width:260px;
   }
 }
