*{
    margin: 0;
    padding: 0;
    /* box-sizing: border-box; */
}
.input-btn{
    background-color: rgb(43, 148, 218);
    color: white;
    outline: none;
    border: none;
    padding-left: 10px;
    padding-right: 10px;
 height: 30px;
}

.top-input{
    background-color: rgba(159, 178, 190, 0.747);
    height:80px;
    
}
.select-input{
    margin-right: 10px;
    height: 35px;
    outline: none;
    border: none;
}
.input-description {
    margin-right: 10px;
    height: 30px;
    width: 300px;
    outline: none;
    border: none;
}
.input-money{
    margin-right: 10px;
    height: 30px;
    width: 100px;
    outline: none;
    border: none;
}
@media screen and (max-width: 600px) {
    .top-input{
        height:140px;
    }
    .input-money{
       margin-top: 20px;
        margin-right: 10px;
        height: 30px;
        width: 100px;
        outline: none;
    }
  }
  @media screen and (max-width: 540px) {
    .input-btn{
       margin-top: 10px;
       }
    .input-description{
        margin-top: 10px;
    }
    .input-money{
       margin-top: 10px;
        margin-right: 10px;
        height: 30px;
        width: 100px;
        outline: none;
    }
  }

  @media screen and (max-width: 400px) {
    .top-input{
        height:160px;
    }
    .input-description{
        margin-top: 10px;
    }
    .input-money{
       margin-top: 10px;
        margin-right: 10px;
        height: 30px;
        width: 100px;
        outline: none;
    }
  }
  
  @media screen and (max-width: 280px) {
    
    .input-description{
        margin-top: 10px;
        width: 200px;
    }
 
  }
  